




























































import { Component, Vue } from 'vue-property-decorator';
@Component({
  components: {},
})
export default class ViewComponent extends Vue {
  /**data */
  items:any = []
  /**computed */
  get options(){
     return {
      pullDownRefresh:  {
        threshold: 60,
        stop: 40,
        txt: '更新成功'
      },
      pullUpLoad: {},
      scrollbar: true,
    }
  }

  /**method */
  onPullingDown() {
    // 模拟更新数据
    setTimeout(() => {
      if (Math.random() > 0.5) {
        // 如果有新数据
        this.items.unshift('dd')
      } else {
        // 如果没有新数据
        (this.$refs.scroll as any).forceUpdate()
      }
    }, 1000)
  }
   onPullingUp() {
    // 模拟更新数据
    setTimeout(() => {
      if (Math.random() > 0.5) {
        // 如果有新数据
        let newPage = ['ss','ss']
        this.items = this.items.concat(newPage)
      } else {
        // 如果没有新数据
        (this.$refs.scroll as any).forceUpdate()
      }
    }, 1000)
  }
}
